<template>
  <div class="content">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button size="small" type="warning" icon="el-icon-download" @click="exportLog('local')">导出</el-button>
      <el-button size="small" type="info" icon="el-icon-download" @click="exportLog('all')">全部导出</el-button>
    </div>
    <el-form :model="opForm" ref="opForm" label-width="85px" class="demo-ruleForm" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :lg="6" :xl="4">
          <el-form-item label="起始时间：">
            <el-date-picker type="date" placeholder="选择日期" v-model="opForm.operTimeMin"
                            value-format="yyyyMMdd" format="yyyy-MM-dd"
                            style="width: 100%;"></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="终止时间：">
            <el-date-picker type="date" placeholder="选择日期" v-model="opForm.operTimeMax"
                            value-format="yyyyMMdd" format="yyyy-MM-dd"
                            style="width: 100%;"></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="对象：">
            <el-input v-model.trim="opForm.operNode" placeholder="对象名"></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="操作：">
            <el-select v-model="opForm.operType" placeholder="请选择操作类型" style="width: 100%;">
              <el-option
                v-for="item in level_list"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="模块：">
            <el-input type="text" v-model.trim="opForm.omType" placeholder="模块名"></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-button size="small" type="primary" icon="el-icon-search" @click="search">查询</el-button>
        </el-col>
      </el-row>
    </el-form>
    <br/>
    <el-table
      stripe
      :data="operationLog.data"
      style="width: 100%">
      <el-table-column
        align="center"
        prop="omType"
        label="模块"
        width="200">
      </el-table-column>
      <el-table-column
        align="center"
        prop="operNode"
        label="对象"
        width="120">
      </el-table-column>
      <el-table-column
        align="center"
        prop="operType"
        label="操作"
        width="120">
        <template slot-scope="scope">
          <span>{{operationTypeArray[scope.row.operType]}}</span>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="operTime"
        label="操作时间"
        width="160">
      </el-table-column>
      <el-table-column
        align="center"
        prop="operAction"
        label="操作内容">
      </el-table-column>
      <el-table-column
        align="center"
        prop="userName"
        label="操作人"
        width="120">
      </el-table-column>
      <el-table-column
        align="center"
        prop="operIp"
        label="操作IP"
        width="130">
      </el-table-column>
    </el-table>
    <el-pagination
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      layout="total, prev, pager, next, jumper"
      :total="operationLog.totalPage">
    </el-pagination>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'
  import Api from '../../constants/Api'

  export default {
    data() {
      return {
        currentPage: 1,
        pageSize: 10,
        opForm: {
          operTimeMin: '',
          operTimeMax: '',
          omType: '',
          operType: '',
          operNode: ''
        },
        level_list: [{
          value: 0,
          label: '启动'
        }, {
          value: 1,
          label: '停止'
        }, {
          value: 2,
          label: '重启'
        }, {
          value: 3,
          label: '部署'
        }],
        paramsData: {},
        operationTypeArray: ['启动', '停止', '重启', '部署']
      }
    },
    computed: {
      ...mapState({
        operationLog: state => state.log.operationLog
      })
    },
    activated() {
      const vm = this
      vm.currentPage = 1
      vm.paramsData = {
        limit: vm.pageSize,
        offset: vm.currentPage
      }
      vm.getOperationLog(vm)
    },
    methods: {
      ...mapActions('log', ['getOperationLog', 'exportOpeLog']),
      handleSizeChange(val) {
        const vm = this
        vm.pageSize = val
        vm.currentPage = 1
        vm.paramsData = {
          limit: vm.pageSize,
          offset: vm.currentPage
        }
        vm.getOperationLog(vm)
      },
      handleCurrentChange(val) {
        const vm = this
        vm.currentPage = val
        vm.paramsData = {
          limit: vm.pageSize,
          offset: vm.currentPage
        }
        vm.getOperationLog(vm)
      },
      search() {
        const vm = this
        vm.paramsData = Object.assign({
          limit: vm.pageSize,
          offset: vm.currentPage
        }, vm.opForm)
        vm.getOperationLog(vm)
      },
      getUrl(url, httpData) {
        if (httpData) {
          url += '?'
          Object.keys(httpData).forEach(key => {
            url += `${key}=${httpData[key]}&`
          })
          url = url.substring(0, url.length - 1)
          return url
        } else {
          return url
        }
      },
      exportLog(type) {
        this.paramsData.isAll = type === 'local' ? 'N' : 'Y'
        this.$axios({
          method: 'get',
          url: this.getUrl(Api.logOperationExport, this.paramsData),
          responseType: 'blob'
        }).then(res => {
          this.commonFunc.funDownload(res, 'galaxy.xlsx')
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/common.scss";
</style>



